<template>
  <div class="all">
    <el-button type="danger"
      class="mark" plain
      size="medium"
      @click="markWord">
      
      <div v-if="wordList[n].marked=='0'">标记</div>
      <div v-else>取消标记</div>
      </el-button>
    <el-checkbox
      v-model="displayEnglish"
      id="english">英
    </el-checkbox>
    <el-checkbox
      v-model="displayChinese"
      id="chinese">中
    </el-checkbox>

    <el-card
      v-if="!isSentense">

      <div class="value"
        v-if="displayEnglish">

        <p id="word">
          {{wordInfo.word}}
        </p>
      </div>
      <div class="announce">
        英式读音
        {{wordInfo.uk_phonetic}}
        <el-button
          type="primary" plain
          icon="el-icon-service"
          size="mini" ref="uk"
          @click="ukSpeech">
        </el-button>
        <br>
        美式读音
        {{wordInfo.us_phonetic}}
        <el-button
          type="primary" plain
          icon="el-icon-service"
          size="mini" ref="us"
          @click="usSpeech">
        </el-button>

      </div>
      <h3>释义</h3>

      <div
        v-if="displayChinese">
        <div class="explains">
          <ul
            v-for="(exp,index) in wordInfo.explains"
            :key="index">
            <li>
              {{exp}}
            </li>
          </ul>

        </div>

        <div
          v-if="wordInfo.wordGroup.length">
          <h3>词组</h3>
          <ul
            v-for="(exp,index) in wordInfo.wordGroup"
            :key="index">
            <li>
              {{exp}}
            </li>
          </ul>

        </div>
        <div
          v-if="!this.flag">
          <h3>网络释义及词组</h3>
          <ul
            v-for="(exp,index) in wordInfo.web"
            :key="index">
            <li>
              {{exp.key}}
              {{exp.value}}
            </li>
          </ul>

        </div>

        <div
          v-if="wordInfo.sentences.length">
          <h3>双语例句</h3>
          <ul
            v-for="(exp,index) in wordInfo.sentences"
            :key="index">
            <li>
              {{exp.en}}
              {{exp.cn}}
            </li>
          </ul>
        </div>
      </div>

    </el-card>

    <el-card
      v-if="isSentense">
      <h2>句子</h2>
      <h4
        v-if="displayEnglish">
        {{sentence.query}}
      </h4>

      <br>
      <h2>释义</h2>
      <h4
        v-if="displayChinese">
        {{sentence.translation}}
      </h4>
      <el-button
        type="primary" plain
        icon="el-icon-service"
        size="mini" ref="uk"
        @click="sentenseSpeech">
      </el-button>
    </el-card>
    <p id="index"
      @click="jump">
      {{n+1}}/{{wordList.length}}
    </p>
    <el-button type="primary"
      plain id="btn1"
      @click="previous">上一个
    </el-button>
    <!-- <el-button type="danger"
      plain id="btn3">标记不会
    </el-button> -->
    <el-button type="success"
      plain id="btn2"
      @click="next">下一个
    </el-button>
  </div>
</template>
<script>
import axios from 'axios'

//代表能否在后台直接查询到单词信息的flag

export default {
  name: 'MyWords',

  data() {
    return {
      currentWord: '',
      wordList: [],
      //好像不能动态的添加属性到对象中去,所以要先定义对象,否则在后面虽然添加进去了,但是在模板中的对象是没有这些属性的,大概是这样吧...
      wordInfo: {
        sentences: [],
        wordGroup: [],
        uk_phonetic: '',
        us_phonetic: '',
        word: '',
        explains: '',
        web: '',
      },
      phoneticArr: [],
      basic: {},
      arr: [],
      keyArr: [],
      flag: true,
      isSentense: false,
      sentence: {},
      n: 0,

      displayEnglish: true,
      displayChinese: true,
    }
  },
  methods: {
    markWord() {
      axios({
        method: 'post',
        url: this.$baseURL+'/word/mark',
        data:{
          wid: this.wordList[this.n].wid,
        },
      }).then((res) => {
        console.log(res)
        if (res.data.status == 200) {
          
          if(this.wordList[this.n].marked==1){
            this.wordList[this.n].marked=0
          }else{
            this.wordList[this.n].marked=1
          }
          
          this.open2(res.data.message)
        } else {
          this.$notify.error({
            title: 'error',
            message: "标记失败",
          })
        }
      })
    },
    jump() {
      this.open()
    },
    open() {
      this.$prompt('请输入要跳转的index', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /[0-9]+/,
        inputErrorMessage: '格式不正确,请输入纯数字',
      })
        .then(({ value }) => {
          this.$message({
            type: 'success',
            message: '跳转到' + value,
          })

          if (value > this.wordList.length && value >= 0) {
            this.$message({
              type: 'warning',
              message: 'out of range!',
            })
          } else {
            this.n = value - 1
            this.getWordInfo(this.wordList[this.n].value)
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入',
          })
        })
    },
    open2(msg) {
      this.$message({
        showClose: true,
        message: msg,
        type: 'success',
      })
    },
    getWordInfo(targetWord) {
      this.isSentense = false
      axios({
        method: 'post',
        url: this.$baseURL+'/word/getWordInfo',
        data: {
          value: targetWord,
        },
      }).then((res) => {
        if (res.data.status == 400) {
          //词库里面没有的话就调用有道的接口去翻译
          this.flag = false
          this.getFromYouDao(this.wordList[this.n].value)
          this.wordInfo.sentences = []
          this.wordInfo.wordGroup = []
        } else {
          //有就直接显示
          this.flag = true
          console.log(res)
          this.wordInfo = res.data.data
        }
      })
    },
    next() {
      if (this.n < this.wordList.length - 1) {
        this.n++
        this.getWordInfo(this.wordList[this.n].value)
      } else {
        this.open2('已经是最后一个单词了,今日共背了' + this.wordList.length + '个')
      }
    },
    previous() {
      if (this.n == 0) {
        this.open2('已经到头了')
      } else {
        if (this.n > 0) {
          this.n--
          if (this.n != -1) {
            this.getWordInfo(this.wordList[this.n].value)
          }
        }
      }
    },
    ukSpeech() {
      if (this.flag) {
        this.FileukSpeech()
      } else {
        this.YDukSpeech()
      }
    },
    usSpeech() {
      if (this.flag) {
        this.FileusSpeech()
      } else {
        this.YDusSpeech()
      }
    },

    FileukSpeech() {
      let audio = new Audio()

      audio.src = 'http://121.40.113.226/audio/' + this.wordInfo.word + '_uk.mp3'
      audio.play()
    },
    FileusSpeech() {
      let audio = new Audio()

      audio.src = 'http://121.40.113.226/audio/' + this.wordInfo.word + '_us.mp3'
      audio.play()
    },

    getFromYouDao(word) {
      let key = 'mtKD1CVzlGrj2rcSxK4DzTPz9m4zRscM'
      let appKey = '50649feb1569418e'
      let salt = new Date().getTime()
      let curtime = Date.parse(new Date()) / 1000
      axios({
        method: 'post',
        url: this.$baseURL+'/api/',
        // url: 'api',
        headers: { 'content-type': 'application/x-www-form-urlencoded' },
        data: this.$qs.stringify({
          q: word,
          from: 'en',
          to: 'zh-CHS',
          salt: salt,
          curtime: curtime,
          appKey: appKey,
          sign: this.$sha256(appKey + this.truncate(word) + salt + curtime + key),
          signType: 'v3',
          ext: 'mp3',
          voice: '0',
          strict: 'true',
        }),
      }).then((res) => {
        console.log(res.data)
        //修改显示的卡片
        if (res.data.isWord == false) {
          this.isSentense = true
          this.sentence.query = res.data.query
          this.sentence.translation = res.data.translation
          this.sentence.speakUrl = res.data.speakUrl
        } else {
          this.isSentense = false

          // console.log(res)
          this.basic = res.data.basic
          this.arr = Object.values(this.basic)
          this.keyArr = Object.keys(this.basic)
          //设置读音数组
          let i = 0
          for (i; i < this.keyArr.length; i++) {
            if (this.keyArr[i] == 'uk-phonetic') break
          }
          this.phoneticArr[0] = this.arr[i]

          let j = 0
          for (j; j < this.keyArr.length; j++) {
            if (this.keyArr[j] == 'us-phonetic') break
          }
          this.phoneticArr[1] = this.arr[j]

          console.log(this.phoneticArr)
          //替换有道接口的结果到wordInfo对应的字段
          this.wordInfo.uk_phonetic = this.phoneticArr[0]
          this.wordInfo.us_phonetic = this.phoneticArr[1]
          this.wordInfo.sentences = []
          this.wordInfo.word = word
          this.wordInfo.explains = this.basic.explains

          this.wordInfo.wordGroup = []
          this.wordInfo.web = res.data.web
          console.log(this.wordInfo)
        }
      })
    },
    truncate(q) {
      let len = q.length
      if (len <= 20) return q
      return q.substring(0, 10) + len + q.substring(len - 10, len)
    },

    YDukSpeech() {
      let audio = new Audio()
      //这里由于vue对变量名中含有-死活没法操作,只能强行将对象转换为数组之后再取值了
      let i = 0
      for (i; i < this.keyArr.length; i++) {
        if (this.keyArr[i] == 'uk-speech') break
      }
      audio.src = this.arr[i]
      audio.play()
      // console.log(this.arr[i])
    },
    YDusSpeech() {
      let audio = new Audio()
      //这里由于vue对变量名中含有-死活没法操作,只能强行将对象转换为数组之后再取值了
      let j = 0
      for (j; j < this.keyArr.length; j++) {
        if (this.keyArr[j] == 'us-speech') break
      }
      audio.src = this.arr[j]
      audio.play()
      // console.log(this.arr[j])
    },
    finished(num) {
      alert('今天背了' + num + '条')
    },

    sentenseSpeech() {
      let audio = new Audio()
      audio.src = this.sentence.speakUrl
      audio.play()
    },
  },

  mounted() {
    if (
      this.$store.state.uid == null ||
      this.$store.state.uid == '' ||
      this.$store.state.uid === undefined
    ) {
      alert('请先登录')
      this.$router.push('/pc/login')
    }

    /**
     * 获得别的页面跳转前请求到的单词数据data数组
     *
     */

    let list = Array.from(this.$route.query.data)

    
    this.wordList=list
    this.n = 0

    this.getWordInfo(this.wordList[0].value)
    // console.log(this.wordList)

    // axios({
    //   method: 'post',
    //   url: this.$baseURL+'/word/todayWords',
    //   data: {
    //     uid: this.$store.state.uid,
    //   },
    // }).then((res) => {
    //   this.n = 0
    //   console.log(res)

    //   // this.wordList = res.data.data
    //   res.data.data.forEach((element) => {
    //     this.wordList.push(element.value)
    //   })

    //   this.getWordInfo(this.wordList[0])
    //   if (
    //     this.wordList.length == 0 &&
    //     !(
    //       this.$store.state.uid == null ||
    //       this.$store.state.uid == '' ||
    //       this.$store.state.uid === undefined
    //     )
    //   ) {
    //     alert(
    //       '今天还没有单词要背哦,先去添加单词吧,添加完一般第二天才可以获取,记得今天先背添加的单词哦'
    //     )
    //   }
    // })
  },
}
</script>
<style  scoped>
/* 上一个按钮 */
#btn1 {
  position: fixed;
  left: 8rem;
  bottom: 6.25rem;
  height: 3.125rem;
  width: 12.5rem;
}
/* 下一个按钮 */
#btn2 {
  position: fixed;
  right: 8rem;
  height: 3.125rem;
  bottom: 6.25rem;
  width: 12.5rem;
}

.value {
  margin-top: -2.5rem;
  /* height: 7vh; */
  font-size: 1.8rem;
}

.announce {
  margin-top: -1rem;
  /* height: 5vh; */
}
ul li {
  list-style: none;
}
.card {
  overflow-y: auto; /* 开启滚动显示溢出内容 */
}
/* 下面的单词页标 */
#index {
  margin-left: 8rem;
}
#chinese {
  font-size: 0.9375rem;
  top: 7.5rem;
  right: 2.5rem;
  position: fixed;
}

#english {
  font-size: 0.9375rem;
  right: 0.6rem;
  top: 9.375rem;
  position: fixed;
}
</style>
<style>
.el-message-box {
  width: 18.75rem;
}
.mark {
  font-size: 1rem;
  right: 15rem;
  top: 8rem;
  position: fixed;
}
</style>
